import { Injectable, Injector } from '@angular/core';
import { BaseApi } from '@shared/common/base/base.api';
import { ILiveData, LiveData, LiveDataKey } from '@shared/http/models/live-data.model';
import { Observable } from 'rxjs';
import { isEmpty } from '@core/utils/common.util';
import { filterNoProgressHttpEvent } from '@core/utils/http.util';
import { map, pluck } from 'rxjs/operators';
import { UserService } from '@core/services/user.service';

@Injectable()
export class LiveDataApiService extends BaseApi {
  protected _liveDataUrl: string;

  constructor(
    protected injector: Injector,
    private $user: UserService
  ) {
    super(injector);

    this.$config
      .getOne$<string>('liveDataUrl')
      .subscribe((liveDataUrl) =>
        this._liveDataUrl = liveDataUrl
      );
  }

  public get<T extends LiveDataKey>(key?: T): Observable<LiveData<T>> {
    const url: string = isEmpty(key) ? this._liveDataUrl : `${this._liveDataUrl}`;
    const headers: Record<string, string> = {
      tokenUuid: this.$user.info.tokenUUID,
      deviceId: this.$user.info.deviceId,
    };

    return this.$http
      .get<ILiveData<typeof key>>(url, {}, { headers })
      .pipe(
        filterNoProgressHttpEvent(),
        pluck('data'),
        map((liveData: LiveData<typeof key>) => {
          return key ? liveData?.[key] : liveData;
        })
      );
  }
}
