import {Component, ChangeDetectionStrategy, ChangeDetectorRef, OnDestroy} from '@angular/core';
import {TeamProfileService} from '@presentation/team/team-profile/team-profile.service';
import {Observable, Subscription} from 'rxjs';
import {Subscriptions} from '@core/decorators/subscriptions.decorator';
import {TeamProfileWorkplaceTableMeta} from '@presentation/team/team-profile/team-profile.model';
import {Router} from '@angular/router';
import {MetaApiService} from '@shared/http/services/meta-api.service';
import {filter} from 'rxjs/operators';
import {AppError} from '@core/models/app-errorl.model';

@Component({
  selector: 'app-team-profile',
  templateUrl: './team-profile.component.html',
  styleUrls: ['./team-profile.component.less'],
  providers: [TeamProfileService],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TeamProfileComponent implements OnDestroy {
  @Subscriptions()
  sub: Subscription;
  tabs: string[] = [];
  id$: Observable<number>;
  dataFormat: string = this.$service.dateFormat;
  workplaceTable = {
    pageSize: 10,
    data: [],
    columns: [
      {title: 'Здание'},
      {title: 'Этаж'},
      {title: 'Место'},
      {title: 'Начало'},
      {title: 'Окончание'},
      {title: 'Статус'},
    ]
  };

  constructor(private $service: TeamProfileService, private cdr: ChangeDetectorRef, private $router: Router, private $meta: MetaApiService) {
    this.id$ = $service.userId$;
    this.sub = $service.workplaceTableMeta$.subscribe((tableMeta) => {
      this.workplaceTable.data = tableMeta;
      this.cdr.markForCheck();
    });
    this.sub = $meta.getMeta('profile').pipe(
      filter(profileMeta => !(profileMeta instanceof AppError))
    ).subscribe((profileMeta) => {
      // @ts-ignore
      this.tabs = profileMeta.tabs;
      this.cdr.markForCheck();
    });
  }

  onClick(row: TeamProfileWorkplaceTableMeta): void {
    this.$router.navigateByUrl(`/team/reservations/workplace/view/${row.reservationId}`);
  }

  ngOnDestroy(): void {
  }
}
