import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GlobalLoaderService } from '@core/services/global-loader.service';
import { UserService } from '@core/services/user.service';
import { TeamProfileWorkplaceTableMeta } from '@presentation/team/team-profile/team-profile.model';
import { DictionariesService } from '@shared/dictionaries/dictionaries.service';
import { MeasurementsService } from '@shared/dictionaries/services/measurements.service';
import { IBuilding, IFloor, IUser, IWorkplace, IWorkplaceReservation } from '@shared/http/models/database.model';
import { ReservationsApiService } from '@shared/http/services/reservations-api.service';
import { UserOfficesService } from '@src/app/core/services/user-offices.service';
import { forkJoin, Observable, of } from 'rxjs';
import { map, switchMap, tap } from 'rxjs/operators';

@Injectable()
export class TeamProfileService {
  constructor(
    private $route: ActivatedRoute,
    private $dictionaries: DictionariesService,
    private $userOffices: UserOfficesService,
    private $loader: GlobalLoaderService,
    private $api: ReservationsApiService,
    private $measurements: MeasurementsService,
    private $user: UserService
  ) {}

  private _user: IUser;
  private _workplaceReservations: IWorkplaceReservation[];
  private _workplaces: IWorkplace[];
  private _buildings: IBuilding[];
  private _floors: IFloor[];
  private _statuses: Record<string, any>;
  get userId$(): Observable<number> {
    return this.$route.paramMap.pipe(
      switchMap((params) => of(Number(params.get('userId') || this.$user.info.id)))
    );
  }
  get user$(): Observable < IUser > {
    return this.userId$.pipe(
      tap(() => this.$loader.contentLoading$.next(true)),
      switchMap((id) => this.$dictionaries.getDictionaryItemByKey<IUser>('labels', id)),
      tap((_u) => this._user = _u)
    );
  }
  get workplace$(): Observable < { workplaceReservations: IWorkplaceReservation[]; floors: IFloor[]; buildings: IBuilding[]; workplaces: IWorkplace[]; statuses: any[]; } > {
    return this.user$.pipe(
      switchMap((user) => forkJoin({
        workplaceReservations: this.$api.getWorkplaceReservations({
          labelId: user.id
        }),
        workplaces: this.$dictionaries.getDictionary<IWorkplace[]>('workplaces'),
        floors: this.$userOffices.getMyOrderedFloors<IFloor[]>(),
        buildings: this.$userOffices.getMyOrderedBuildings<IBuilding[]>(),
        statuses: this.$dictionaries.getDictionary('statuses'),
      })),
      tap(({ workplaceReservations, workplaces, floors, buildings, statuses }) => {
        this._workplaceReservations = workplaceReservations;
        this._workplaces = workplaces;
        this._floors = floors;
        this._buildings = buildings;
        this._statuses = statuses[0].statuses.reduce((acc, status) => {
          acc[status.code] = {
            value: status.name,
            color: status.color
          };
          return acc;
        }, {});
      }),
    );
  }
  get workplaceTableMeta$(): Observable<TeamProfileWorkplaceTableMeta[]> {
    return this.workplace$.pipe(
      map(({ workplaceReservations, workplaces, floors, buildings  }) => {
        return workplaceReservations.map((_r) => {
          const workplace = workplaces.find((_w) => _w.id === _r.workplaceId);
          const floor = floors.find((_f) => _f.id === workplace?.floorMapId);
          const building = buildings.find((_b) => _b.id === floor?.buildingId);
          return {
            reservationId: _r.id,
            floorId: floor?.id || 0,
            placeId: workplace?.id || 0,
            building: building?.name || '-',
            floor: floor?.name || '-',
            place: workplace?.name || '-',
            startDate: _r.dateTimeFrom,
            endDate: _r.dateTimeTo,
            status: {
              value: _r.status,
              options: { map: this._statuses }
            }
          };
        });
      }),
      tap(() => this.$loader.contentLoading$.next(false)),
    );
  }
  get workplaceReservations(): IWorkplaceReservation[] {
    return this._workplaceReservations;
  }
  get workplaces(): IWorkplace[] {
    return this._workplaces;
  }
  get floors(): IFloor[] {
    return this._floors;
  }
  get buildings(): IBuilding[] {
    return this._buildings;
  }
  get dateFormat(): string {
    return this.$measurements.getMeasurementByName('dateTime');
  }
  get statuses(): any {
    return this._statuses;
  }
}
